import { Component, OnInit } from '@angular/core';
import { RestApiService } from 'src/app/services/rest-api.service';
import { AuthenticateService } from 'src/app/services/authentication.service';
import { ToastController } from '@ionic/angular';

@Component({
  selector: 'app-posts',
  templateUrl: './posts.page.html',
  styleUrls: ['./posts.page.scss'],
})
export class PostsPage implements OnInit {

  title: string;
  email: string;
  phone: string;
  address: string;
  Alarm: string;
  profess_num:string;
  objs:any;

  constructor(public restApi: RestApiService, public authService: AuthenticateService, public toastController: ToastController) { }

  ngOnInit() {
    this.getDetails();
    this.getProfess();
  }


  getDetails(){
    this.restApi.post('professional/get-details', { }).subscribe((res: any) => {
      if (res && res.status) {
        try{
          console.log(res.data);
          this.title = res.data['0']['title'];
          this.email = res.data['0']['email'];
          this.phone = res.data['0']['number'];
          this.address = res.data['0']['address'];
          this.Alarm = res.data['0']['Alarm'];
        }
        catch (Error) {
          this.toastController.create({
            message: 'Something went wrong.',
            duration: 2000
          }).then(toast => toast.present());
        }        
      }
    }, error => {
      this.toastController.create({
        message: 'Something went wrong.',
        duration: 2000
      }).then(toast => toast.present());
    });
  }

  toast(message) {
    this.toastController.create({
      message: message,
      duration: 2000
    }).then(toast => toast.present());
  }

  getProfess() {
    this.restApi.post('professional/get-profess', {}).subscribe((res: any) => {
      if (res && res.status) {
        if (res.status == 'success') {
          this.objs = res.data;
          console.log(this.objs);
        } else {
          this.toastController.create({
            message: res.message,
            duration: 2000
          }).then(toast => toast.present());
        }
      }
    }, error => {
      this.toastController.create({
        message: 'Something went wrong.',
        duration: 2000
      }).then(toast => toast.present());
    });
  }    
}
